import React from 'react'
import * as Colors from '../../lib/utils/colors'
import Link from 'gatsby-link'
import kebabCase from 'lodash/kebabCase'

const styles = {
  tag: {
    marginRight: 10,
    backgroundColor: 'grey',
    padding: 5,
    paddingLeft: 8,
    paddingRight: 8,
    borderRadius: 6,
    fontWeight: 'bold',
    fontFamily: 'Raleway',
    textDecoration: 'none',
  },
};

export default function Tag({ item, count }) {
  const color = Colors.tags[item];
  let style = { ...styles.tag };
  if (color) {
    style.color = color;
    style.backgroundColor = Colors.alpha(color, 0.3);
  }

  return (
    <Link
      style={style}
      to={`/tags/${kebabCase(item)}`}
    >
      {item} {count && `(${count})`}
    </Link>
  );
}



