import React, { Component } from 'react';
import AppBar from 'material-ui/AppBar';
import IconButton from 'material-ui/IconButton';
import ArrowBack from 'material-ui/svg-icons/navigation/arrow-back';
import MenuIcon from 'material-ui/svg-icons/navigation/menu';
import './index.css'
import Link, { navigateTo } from "gatsby-link";
import FlatButton from 'material-ui/FlatButton';
import { slide as Menu } from 'react-burger-menu';

const styles = {
  navLink: {
    color: 'black',
    fontFamily: 'Raleway',
    textDecoration: 'none',
  },
  aboutLink: {
    marginLeft: 15,
  },
  learn: {
    backgroundColor: '#0069d9',
    color: 'white',
    padding: 10,
    borderRadius: 6,
  },
  menuLinkItem: {
    color: 'black',
    fontFamily: 'Raleway',
    textDecoration: 'none',
    paddingBottom: 20,
  },
};

class NavbarFixed extends Component {
  state = {
    menuOpen: false,
    isMobile: false,
  }

  componentDidMount() {
    window.addEventListener("resize", this.resize.bind(this));
    this.resize();
  }

  resize() {
    this.setState({ isMobile: window.innerWidth <= 760 });
  }

  handleStateChange (state) {
    this.setState({menuOpen: state.isOpen});
  }

  closeMenu () {
    this.setState({menuOpen: false});
  }

  toggleMenu () {
    this.setState({menuOpen: !this.state.menuOpen});
  }

  renderIconElementRight() {
    const { showBlogLink } = this.props;
    if (!showBlogLink) return null;

    if (this.state.isMobile) {
      return (
        <div style={{ position: 'absolute', top: 0, right: 20, zIndex: 9999, fontSize: 14}}>
          <Menu
            right
            customBurgerIcon={false}
            // customCrossIcon={false}
            onStateChange={(state) => this.handleStateChange(state)}
            isOpen={this.state.menuOpen}
            width="100%"
          >
            <Link to="/learn" style={styles.menuLinkItem}>
              LEARN REACT NATIVE
            </Link>
            <Link to="/blog" style={styles.menuLinkItem}>
              BLOG
            </Link>
            <Link to="/about" style={styles.menuLinkItem}>
              ABOUT
            </Link>
          </Menu>
        </div>
      );
    }

    return (
      <div style={{ position: 'absolute', top: 20, right: 20, zIndex: 9999, fontSize: 14, }}>
        <Link to="/learn" style={{...styles.navLink, ...styles.learn}}>
          LEARN REACT NATIVE
        </Link>
        <Link to="/blog" style={{...styles.navLink, ...styles.aboutLink}}>
          BLOG
        </Link>
        <Link to="/about" style={{...styles.navLink, ...styles.aboutLink}}>
          ABOUT
        </Link>
      </div>
    );
  }

  renderTitle() {
    const { title, canGoBack, backToUrl } = this.props;
    const toUrl = backToUrl || '/';
    let newTitle = title;
    const nameColor = '#0069d9';

    if (title === 'CHIN LOONG TAN') {
      newTitle = (
        <div>
          <span style={{ color: nameColor }}>
            CHIN LOONG
          </span>
          {' TAN'}
        </div>
      );
    }

    return (
      <Link to={toUrl} style={styles.navLink}>
        {newTitle}
      </Link>
    );
  }

  renderIconElementLeft() {
    const { canGoBack, backToUrl, onPress } = this.props;
    if (!canGoBack) {
      return (
        <IconButton
          disableTouchRipple
          onClick={() => this.toggleMenu()}
        >
          <MenuIcon color="black" />
        </IconButton>
      );
    }

    if (onPress) {
      return (
        <IconButton
          disableTouchRipple
          onClick={onPress}
        >
          <ArrowBack color="black" />
        </IconButton>
      );
    }

    return (
      <Link to={backToUrl}>
        <IconButton disableTouchRipple>
          <ArrowBack color="black" />
        </IconButton>
      </Link>
    );
  }

  render() {
    const { backToUrl, canGoBack } = this.props;

    return (
      <div className="custom-nav">
        <AppBar
          title={this.renderTitle()}
          iconElementLeft={this.renderIconElementLeft()}
          iconElementRight={this.renderIconElementRight()}
          titleStyle={{
            color: 'black',
            fontFamily: 'Raleway',
            fontSize: 18,
          }}
          style={{
            backgroundColor: 'white',
          }}
          showMenuIconButton={canGoBack || (!canGoBack && this.state.isMobile)}
        />
      </div>
    );
  }
}

export default NavbarFixed;



